@import url("https://fonts.googleapis.com/css?family=Montserrat:400,500,600|Nunito:400,600,700");

/*======================
        1.BASIC
========================*/

body {
  font-family: "Nunito", sans-serif;
  overflow-x: hidden;
  color: #fbfbfb;
  background: #2e3135;
}

h1,
h2,
h3,
h4,
h5,
h6 {
  font-family: "Montserrat", sans-serif;
}

a,
a:hover,
a:focus,
button,
button:focus {
  text-decoration: none !important;
  outline: none !important;
  box-shadow: none !important;
}

::selection {
  background: rgba(166, 175, 189, 0.3);
}

::-moz-selection {
  background: rgba(166, 175, 189, 0.3);
}

#preloader {
  position: fixed;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  background-color: #26282b;
  z-index: 9999999;
}

#status {
  width: 40px;
  height: 40px;
  position: absolute;
  left: 50%;
  top: 50%;
  margin: -20px 0 0 -20px;
}

.spinner {
  margin: 0 auto;
  font-size: 10px;
  position: relative;
  text-indent: -9999em;
  border-top: 5px solid #2e3135;
  border-right: 5px solid #2e3135;
  border-bottom: 5px solid #2e3135;
  border-left: 5px solid #6b6e6f;
  -webkit-transform: translateZ(0);
  -ms-transform: translateZ(0);
  transform: translateZ(0);
  -webkit-animation: load8 1.1s infinite linear;
  animation: load8 1.1s infinite linear;
}

.spinner,
.spinner:after {
  border-radius: 50%;
  width: 40px;
  height: 40px;
}

@-webkit-keyframes load8 {
  0% {
    -webkit-transform: rotate(0deg);
    transform: rotate(0deg);
  }
  100% {
    -webkit-transform: rotate(360deg);
    transform: rotate(360deg);
  }
}

@keyframes load8 {
  0% {
    -webkit-transform: rotate(0deg);
    transform: rotate(0deg);
  }
  100% {
    -webkit-transform: rotate(360deg);
    transform: rotate(360deg);
  }
}

.back_top {
  text-align: center;
  position: fixed;
  bottom: 15px;
  height: 35px;
  width: 35px;
  display: none;
  z-index: 10000;
  border-radius: 3px;
  right: 20px;
  transition: all 0.5s;
}

.back_top i {
  color: #fff;
  line-height: 35px;
  font-size: 32px;
  display: block;
}

/*==========================
        2.HELPER
============================*/

.section {
  padding-top: 100px;
  padding-bottom: 100px;
  position: relative;
  background-color: #2e3135;
}

.bg-light {
  background-color: #26282b !important;
}

.text-muted {
  color: #a0a0ac !important;
}

.img-thumbnail {
  background-color: #3b3e42;
  border: 1px solid #4d5052;
}

.h-100vh {
  height: 100vh;
}

.z-index {
  z-index: 2;
}

.clippath_none {
  clip-path: none !important;
}

.section-subtitle {
  max-width: 500px;
}

.btn {
  padding: 15px 32px;
  font-size: 14px;
  font-weight: 700;
  transition: all 0.5s;
  letter-spacing: 0.6px;
  color: #fff;
  box-shadow: none !important;
  text-transform: uppercase;
  outline: none !important;
}

.btn-round {
  border-radius: 30px;
}

.btn-custom {
  border: 1px solid;
}

.btn-custom:hover,
.btn-custom:focus,
.btn-custom:active,
.btn-custom.active,
.btn-custom.focus,
.btn-custom:active,
.btn-custom:focus,
.btn-custom:hover,
.open > .dropdown-toggle.btn-custom {
  color: #f3f3f3;
}

.btn-outline-custom {
  border: 2px solid #f5f5f5;
}

.btn-outline-custom:hover,
.btn-outline-custom:focus,
.btn-outline-custom:active,
.btn-outline-custom.active,
.btn-outline-custom.focus,
.btn-outline-custom:active,
.btn-outline-custom:focus,
.btn-outline-custom:hover,
.open > .dropdown-toggle.btn-outline-custom {
  color: #000;
  background-color: #fff;
  border-color: #fff;
}

.bg-overlay {
  background-color: rgba(38, 40, 43, 0.72);
  position: absolute;
  top: 0;
  right: 0;
  width: 100%;
  height: 100%;
}

/*==========================
        3.NAVBAR CUSTOM
============================*/

.custom-nav {
  background-color: transparent;
  padding: 20px 0px;
  width: 100%;
  border-radius: 0px;
  z-index: 999;
  margin-bottom: 0px;
  transition: all 0.5s ease-in-out;
}

.custom-nav .navbar-nav li a {
  color: #fff !important;
  font-size: 15px;
  background-color: transparent !important;
  margin: 0 7px;
  letter-spacing: 0.05em;
  line-height: 24px;
  transition: all 0.5s;
  font-weight: 500;
}

.custom-nav .navbar-brand .logo-light,
.custom-nav .navbar-brand .logo-dark {
  height: 27px;
}

.logo .logo-light {
  display: inline-block;
  font-family: "-apple-system, BlinkMacSystemFont", "Segoe UI", "Roboto",
    "Oxygen", "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
}

.stickyadd .logo .logo-dark {
  display: inline-block;
}

.stickyadd .logo .logo-light {
  display: none;
}

.logo .logo-dark {
  display: none;
  color: black;
  font-family: "-apple-system, BlinkMacSystemFont", "Segoe UI", "Roboto",
    "Oxygen", "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
}

.custom-nav .navbar-brand {
  color: #fff !important;
  text-transform: uppercase;
}

.custom-nav .navbar-toggler {
  color: #fff;
  font-size: 35px;
  padding: 0px;
}

.custom-nav.stickyadd {
  background-color: #fff !important;
  box-shadow: 0 1px 8px 3px rgba(0, 0, 0, 0.051);
  padding: 15px 0px;
}

.custom-nav.stickyadd .navbar-nav li a {
  color: #000 !important;
}

/*===========================
        4.HOME
=============================*/

.header-bg-img {
  background-image: url("./images/header-bg3.jpg");
  position: relative;
  background-size: cover;
  background-position: center center;
}

.header-content {
  max-width: 800px;
}

.header-table-center {
  display: table-cell;
  vertical-align: middle;
}

.header-table {
  display: table;
  width: 100%;
  height: 100%;
}

.header-name {
  font-size: 54px;
}

.header-desc {
  max-width: 500px;
  line-height: 1.8;
  color: rgba(255, 255, 255, 0.75) !important;
}

.scroll_down {
  position: absolute;
  bottom: 5vh;
  width: 100%;
  text-align: center;
  color: #fff;
  font-size: 13px;
  z-index: 8;
}

.scroll_down i {
  position: relative;
  -webkit-animation: bounce 2s infinite;
  animation: bounce 2s infinite;
  height: 44px;
  width: 28px;
  line-height: 54px;
  border-radius: 30px;
  border: 2px solid #ffffff;
  display: inline-block;
  color: #fff;
  font-size: 10px;
}

.scroll_down i:after {
  content: "";
  position: absolute;
  top: 22px;
  left: 11px;
  border-radius: 2px;
  z-index: -1;
  bottom: 0px;
  transition: all 0.2s;
  height: 10px;
  width: 3px;
  background: #fff;
}

/*HOME 4*/

.curv-img {
  position: absolute;
  width: 100%;
  bottom: 0px;
  z-index: 1;
}

.curv-img svg {
  display: block;
}

/*HOME 7*/

.clip-home {
  clip-path: polygon(0 0, 100% 0, 100% 83%, 50% 100%, 0 83%);
  position: relative;
}

/*DEMO 16*/

.wave_sec {
  position: absolute;
  bottom: 0;
  width: 100%;
}

.wave_sec svg {
  height: 180px;
}

/*===========================
        5.ABOUT
=============================*/

.about-social li a {
  color: #a0a0ac;
  display: inline-block;
  height: 42px;
  width: 42px;
  font-size: 20px;
  border-radius: 50%;
  border: 2px solid #6b6e6f;
  line-height: 38px;
  transition: all 0.5s;
  text-align: center;
}

/*===========================
        6.SERVICES
=============================*/

.services-boxes {
  border: 1px solid #353941;
}

.services-boxes .services-boxes-icon i {
  font-size: 44px;
}

.services-boxes .services-title-border {
  height: 2px;
  width: 40px;
  margin: 15px auto;
  transition: all 0.5s;
}

.services-boxes:hover .services-title-border {
  width: 60px;
}

.service-box {
  border: 1px solid #eaeaea;
  border-radius: 8px;
}

.services-boxes h5 {
  font-weight: 600;
}

.service-box .service-content h5 {
  font-size: 20px;
  font-weight: 700;
}

.service-box .service-content p {
  font-size: 15px;
}

/*===========================
        7.CLIENT
=============================*/

.testi_boxes {
  max-width: 750px;
}

.client_review {
  font-size: 18px;
}

.client_name {
  font-size: 16px;
}

.testi_boxes .tam_testi_icon {
  font-size: 50px;
}

.owl-theme .owl-controls .owl-page {
  margin: 20px 0px;
}

.owl-theme .owl-controls .owl-page span {
  display: block;
  width: 12px;
  height: 6px;
  margin: 5px 4px;
  filter: Alpha(Opacity=50);
  opacity: 0.5;
  -webkit-border-radius: 20px;
  -moz-border-radius: 20px;
  border-radius: 20px;
  transition: all 0.5s;
}

.owl-theme .owl-controls .owl-page.active span {
  width: 20px;
}

/*===========================
        8.WORK
=============================*/

.work_menu li a {
  cursor: pointer;
  display: block;
  color: #fff;
  border-radius: 30px;
  letter-spacing: 0.03em;
  margin-bottom: 5px;
  font-weight: 600;
  font-size: 14px;
  padding: 6px 16px;
  transition: all 0.5s;
  margin-top: 5px;
}

.work_menu a.active,
.work_menu a:hover {
  color: #fff !important;
}

.work_item {
  position: relative;
  padding: 15px;
}

.work_box {
  overflow: hidden;
  position: relative;
}

.work_box .work_img {
  position: relative;
  overflow: hidden;
}

.work_box .work_img:after {
  content: " ";
  display: block;
  width: 100%;
  height: 100%;
  position: absolute;
  top: 0;
  left: 0;
  border-radius: 5px;
  transition: all 0.27s cubic-bezier(0.3, 0.1, 0.58, 1);
}

.work_box .work_img > img {
  transition: all 0.3s cubic-bezier(0.3, 0.1, 0.58, 1);
  border-radius: 0;
}

.work_box .work_detail {
  opacity: 0;
  color: #ffffff;
  width: 100%;
  padding: 20px;
  box-sizing: border-box;
  position: absolute;
  left: 0;
  overflow: hidden;
  transition: all 0.27s cubic-bezier(0.3, 0.1, 0.58, 1);
}

.work_box .work_detail h4 {
  font-size: 18px;
}

.work_box .work_detail p {
  color: rgba(255, 255, 255, 0.6);
  font-size: 14px;
}

.work_box:hover .work_detail {
  top: 50%;
  transform: translate(0, -50%);
  opacity: 1;
}

.work_box:hover .work_img:after {
  background: rgba(38, 40, 43, 0.72);
}

.work_box:hover .work_img > img {
  transform: scale(1.05);
}

/*=========================
        9.CONTACT
===========================*/

.blog_color {
  background-color: #2e3135;
}

/*=========================
        10.CONTACT
===========================*/

.form-kerri .form-control {
  box-shadow: none !important;
  height: 50px;
  border: 2px solid #3b3e42;
  background-color: transparent;
  color: #fbfbfb;
}

.form-kerri textarea.form-control {
  height: auto;
}

.contact_detail-title {
  font-size: 18px;
}

.error {
  margin: 8px 0px;
  display: none;
  color: #ec5f79;
}

.error_msg {
  margin-bottom: 20px;
  text-align: center;
  font-size: 18px;
  color: #ec5f79;
  font-weight: 700;
}

.gig_loader {
  display: none;
}

#success_msg {
  text-align: center;
  margin-bottom: 20px;
}

#success_msg h3 {
  color: #64d674;
  font-size: 22px;
}

/*======================
        11.FOOTER
========================*/

.footer {
  padding: 70px 0px;
  background-color: #26282b !important;
}

.footer .footer-alt p {
  font-size: 16px;
}

.footer .footer-alt ul li a {
  border-radius: 50%;
  transition: all 0.5s;
  background-color: rgba(241, 241, 241, 0.08);
  width: 42px;
  height: 42px;
  display: block;
  font-size: 18px;
  line-height: 42px;
  text-align: center;
}

/*===========================
        12.RESPONSIVE
=============================*/

@media (min-width: 200px) and (max-width: 768px) {
  .custom-nav {
    background-color: #fff !important;
    padding: 10px 0px !important;
  }
  .custom-nav .container {
    width: 85%;
  }
  .custom-nav .navbar-toggler {
    color: #000;
    font-size: 32px;
    border: none;
  }
  .custom-nav .logo .logo-dark {
    display: inline-block;
  }
  .custom-nav .navbar-nav li a {
    color: rgba(0, 0, 0, 0.6) !important;
  }
  .custom-nav .logo .logo-light {
    display: none;
  }
  .h-100vh {
    height: auto;
    padding: 120px 0 140px;
  }
  .header-bg-img {
    clip-path: none;
    padding: 120px 0 140px;
  }
  .wave-home.h-100vh {
    height: auto;
    padding: 120px 0 190px;
  }
  .custom-nav .navbar-nav li a {
    margin: 0 0px;
    line-height: 15px;
  }
  .header-name {
    font-size: 38px;
  }
  .clip-home {
    clip-path: none;
  }
}

@media (max-width: 2560px) {
  .home-bg .curv-img {
    bottom: -5px;
  }
}

#color-switcher div h3 {
  font-size: 16px;
  margin: 8px 3px 12px;
  color: #000;
}

#color-switcher {
  background: none repeat scroll 0 0 #ffff;
  box-shadow: 0px 0px 14px 0 rgba(0, 0, 0, 0.08);
  left: -189px;
  position: fixed;
  top: 17%;
  width: 189px;
  z-index: 9999;
  padding: 10px 5px;
}

#color-switcher div {
  padding: 5px 10px;
}

#color-switcher .bottom {
  background: none repeat scroll 0 0 #fff;
  color: #252525;
  padding: 0;
}

#color-switcher .bottom a.settings {
  background: none repeat scroll 0 0 #2b2424;
  box-shadow: 2px 2px 0 0 rgba(0, 0, 0, 0.08);
  display: block;
  height: 41px;
  position: absolute;
  right: -40px;
  top: 0;
  width: 40px;
  padding: 3px;
}

#color-switcher .bottom a {
  text-decoration: none;
}

#color-switcher .bottom a.settings i {
  margin-left: 5px;
  color: #fff;
  font-size: 24px;
  position: absolute;
}

ul.pattern {
  list-style: none outside none;
  margin: 0 0 0;
  overflow: hidden;
  padding: 0;
  border-radius: 0;
}

ul.pattern li {
  float: left;
  margin: 2px;
}

ul.pattern li a {
  cursor: pointer;
  display: block;
  height: 35px;
  width: 35px;
  border-radius: 16px 16px 0;
  margin: 5px;
}

ul.pattern .color1 {
  background: #e65f78;
}

ul.pattern .color2 {
  background: #37b8df;
}

ul.pattern .color3 {
  background: #31b164;
}

ul.pattern .color4 {
  background: #5d69f6;
}

ul.pattern .color5 {
  background: #87ceeb;
}

ul.pattern .color6 {
  background: #f2b31a;
}

@media only screen and (max-width: 479px) {
  #color-switcher {
    display: none;
  }
}
